*, ::before, ::after{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  scroll-behavior: smooth;
}

:root {
  --blue: #0096FF;
  --dark: #3D3838;
  --grayMd: #A9A7A7;
  --grayLt: #E7E7E7;
  
}

#contact {
  scroll-margin-top: 125px !important;
}

/* ---------------------------------------------------------------- */
/* NavigationFix */
#services, #portfolio{
  scroll-margin-top: 125px;
}

/* ---------------------------------------------------------------- */
/* ScrollBar */
::-webkit-scrollbar {
  width:10px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--blue);
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background-color: var(--grayLt);

}

/* ---------------------------------------------------------------- */
/* Preload: */
.preloadContainer{
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
  background-color: var(--blue);
}

.preloadContainer .svg{
  position: absolute;
  scale: 150%;
  color: white;
}

.preloadContainer .loading{

  outline: 2px solid white;
  background-color: white;
  margin-top: 150px;
  max-width: 160px;
  width: 100%;
  height: 3px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
}

.preloadContainer .loading .loader{
  position: absolute;
  width: 160px;
  height:  3px;
  left: 0;
  background-color: var(--grayMd);
  animation: progress 0.85s ease-in-out forwards;
  border-radius: 20px;
}

@keyframes progress {
  0%{
    width: 0;
    opacity: 1;
    
  }
  100%{
    width:100%;
    opacity: 1;
  }
}

/* ---------------------------------------------------------------- */

svg{
  width: 100%;
}

image{
  width: 100%;
}